
@import url(https://fonts.googleapis.com/css?family=Playfair+Display);


* {
  margin: 0;
  padding: 0;
}

.card-wrapper {
  padding: 2%;
  columns: 5;

}
button{
  background-color: red;
  padding: 5px;
  border: none;
  border-radius: 3px;
  color: aliceblue;
}

.card-wrapper .card{
  margin: 4px auto;
 
}

.card {
  position :relative;
  max-width: 300px;
  background-color: #FEEFDE;
  border-radius: 8px;
  overflow: hidden;
  padding-bottom: 10px;
  box-shadow: 1px 2px 8px rgba(100, 100, 100, 0.1);
 
}

.image-wrapper {
  background-color: red;
  width: auto;
  height: 300px;
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}

img:hover {
 -webkit-transform: scale3d(1.2, 1.2, 1);
  transform: scale3d(1.2, 1.2, 1);
  transition: all 1s linear;
}

.text-box-wrapper {
 padding: 20px 
}

.heading {
  font-family: 'Playfair-Display';
  font-size: 36px;
  font-weight: 500;
  line-height: 1;
}


.text {
  font-family: 'Poppins', sans-serif;
  font-weight: 100;
  font-size: 12px;
  padding-top: 10px;
  line-height: 2;
}


